<template>
    <div class="p-grid">
        <div class="p-col-12 p-md-6">
            <p-card>
                <template #content>
                    <div class="p-fluid p-formgrid p-grid">
                        <div class="p-field p-col-12 p-md-6">
                            <label for="cycle-type" class="p-mb-1">
                                Cryoprogram
                            </label>
                            <p-input-text
                                v-model="form.cryoProgram"
                                id="cycle-type"
                                type="text"
                            />
                        </div>
                        <div class="p-field p-col-12 p-md-6">
                            <label for="cycle" class="p-mb-1">
                                Cryoprotectant
                            </label>
                            <p-input-text
                                v-model="form.cryoProtectant"
                                id="cycle"
                                type="text"
                            />
                        </div>
                    </div>

                    <div class="p-fluid p-formgrid p-grid">
                        <div class="p-col-12 p-md-4 p-pt-2">
                            Strwas available: {{ form.totalStrawsAvlbl }}
                        </div>
                        <div class="p-col-12 p-md-4 p-pt-2">
                            Embryos available: {{ form.totalEmbryosAvlbl }}
                        </div>
                        <div class="p-col-12 p-md-4">
                            <p-button
                                label="View Selected Embryo"
                                class="p-button-secondary"
                            />
                        </div>
                    </div>
                </template>
            </p-card>
        </div>
    </div>

    <div class="p-grid">
        <div class="p-col-12 p-md-4">
            <p-panel header="Freezing Comments">
                <div class="p-fluid p-formgrid p-grid">
                    <div class="p-field p-col-12 p-md-12">
                        <label for="f-comments" class="p-mb-1">
                            Comments
                        </label>
                        <p-textarea
                            v-model="form.freezingComment"
                            id="f-comments"
                            type="text"
                        />
                    </div>
                    <div class="p-field p-col-12 p-md-6">
                        <label for="fc-embryologist" class="p-mb-1">
                            Embryologist
                        </label>
                        <p-dropdown
                            v-model="form.freezingEmbryologist"
                            id="fc-embryologist"
                            :options="providers"
                            filter
                            optionLabel="name"
                            optionValue="id"
                        />
                    </div>
                    <div class="p-field p-col-12 p-md-6">
                        <label for="fc-clinicin" class="p-mb-1">
                            Clinician
                        </label>
                        <p-dropdown
                            v-model="form.freezingClinician"
                            id="fc-clinicin"
                            :options="providers"
                            filter
                            optionLabel="name"
                            optionValue="id"
                        />
                    </div>
                </div>
            </p-panel>
        </div>

        <div class="p-col-12 p-md-4">
            <p-panel header="Thawing Comments">
                <div class="p-fluid p-formgrid p-grid">
                    <div class="p-field p-col-12 p-md-12">
                        <label for="t-comments" class="p-mb-1">
                            Comments
                        </label>
                        <p-textarea
                            v-model="form.thawingComment"
                            id="t-comments"
                            type="text"
                        />
                    </div>
                    <div class="p-field p-col-12 p-md-6">
                        <label for="tc-embryologist" class="p-mb-1">
                            Embryologist
                        </label>
                        <p-dropdown
                            v-model="form.thawingEmbryologist"
                            id="tc-embryologist"
                            :options="providers"
                            filter
                            optionLabel="name"
                            optionValue="id"
                        />
                    </div>
                    <div class="p-field p-col-12 p-md-6">
                        <label for="tc-clinicin" class="p-mb-1">
                            Clinician
                        </label>
                        <p-dropdown
                            v-model="form.thawingClinician"
                            id="tc-clinicin"
                            :options="providers"
                            filter
                            optionLabel="name"
                            optionValue="id"
                        />
                    </div>
                </div>
            </p-panel>
        </div>

        <div class="p-col-12">
            <p-button
                label="Save"
                class="p-button-success p-mr-2"
                @click="save"
                v-if="hasUpdatePermission"
            />
            <p-button
                label="Delete"
                class="p-button-danger"
                @click="deleteSemenCryoStorage"
                v-if="hasDeletePermission"
            />
        </div>
    </div>
</template>

<script>
import { useSemenCryoStorageFormInit } from "@/composable/semen-cryo-storage";
import { onMounted, watch } from "vue-demi";
import { usePermissionCheck } from "@/composable/users";
export default {
    setup() {
        const {
            providers,
            form,
            fillForm,
            semenCryoStorage,
            save,
            deleteSemenCryoStorage
        } = useSemenCryoStorageFormInit();

        const {
            hasUpdatePermission,
            hasDeletePermission
        } = usePermissionCheck();

        watch(semenCryoStorage, fillForm);

        onMounted(() => {
            if (semenCryoStorage.value) {
                fillForm(semenCryoStorage.value);
            }
        });

        return {
            form,
            providers,
            save,
            deleteSemenCryoStorage,
            hasUpdatePermission,
            hasDeletePermission
        };
    }
};
</script>
