import { useToast } from "primevue/usetoast";
import { computed, reactive } from "vue";
import { useStore } from "vuex";

export function useSemenCryoStorageInit() {
    const store = useStore();
    const toast = useToast();

    const getProvivers = () => {
        store.dispatch("provider/getProviders");
    };

    const getSemenCryoStorage = () => {
        if (!store.state.artCycle.selectedCycle) {
            toast.add({
                severity: "warn",
                summary: "Warnning!",
                detail: "Please select cycle first",
                life: 3000
            });
            return;
        }

        store
            .dispatch(
                "semenCryoStorage/getSemenCryoStorage",
                store.state.artCycle.selectedCycle.cycleId
            )
            .then(() => {})
            .catch((err) => {});
    };

    return { store, getSemenCryoStorage, getProvivers };
}

export function useSemenCryoStorageFormInit() {
    const store = useStore();
    const toast = useToast();
    const providers = computed(() => store.state.doctor.embryologists);
    const semenCryoStorage = computed(
        () => store.state.semenCryoStorage.semenCryoStorage
    );
    const form = reactive({
        ecId: 0,
        cryoProgram: null,
        cryoProtectant: null,
        totalStrawsAvlbl: null,
        totalEmbryosAvlbl: null,
        freezingEmbryologist: null,
        freezingComment: null,
        freezingClinician: null,
        thawingEmbryologist: null,
        thawingComment: null,
        thawingClinician: null
    });

    const fillForm = (data) => {
        if (data) {
            form.ecId = data.ecId;
            form.cryoProgram = data.cryoProgram;
            form.cryoProtectant = data.cryoProtectant;
            form.totalStrawsAvlbl = data.totalStrawsAvlbl;
            form.totalEmbryosAvlbl = data.totalEmbryosAvlbl;
            form.freezingEmbryologist = data.freezingEmbryologist;
            form.freezingComment = data.freezingComment;
            form.freezingClinician = data.freezingClinician;
            form.thawingEmbryologist = data.thawingEmbryologist;
            form.thawingComment = data.thawingComment;
            form.thawingClinician = data.thawingClinician;
        } else {
            form.ecId = 0;
            form.cryoProgram = null;
            form.cryoProtectant = null;
            form.totalStrawsAvlbl = null;
            form.totalEmbryosAvlbl = null;
            form.freezingEmbryologist = null;
            form.freezingComment = null;
            form.freezingClinician = null;
            form.thawingEmbryologist = null;
            form.thawingComment = null;
            form.thawingClinician = null;
        }
    };

    const save = () => {
        const data = {
            ...form,
            ...store.getters["artCycle/getSelectedCycleAndPatientId"]
        };
        store
            .dispatch("semenCryoStorage/saveSemenCryoStorage", data)
            .then(() => {})
            .finally(() => {});
    };

    const deleteSemenCryoStorage = () => {
        store
            .dispatch("semenCryoStorage/deleteSemenCryoStorage", form.ecId)
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "Success!",
                    detail: "Deleted successfully",
                    life: 3000
                });
            });
    };

    return {
        providers,
        form,
        fillForm,
        semenCryoStorage,
        save,
        deleteSemenCryoStorage
    };
}
