<template>
    <p-fieldset legend="Embryo Cryo Storage" id="semen-cryo-storage">
        <semen-cryo-storage-form />

        <p-data-table
            :value="table"
            responsiveLayout="scroll"
            showGridlines
            class="p-datatable-sm p-mt-1 p-mb-2"
        >
            <p-column header="Freez Date" field="freezeDate" />
            <p-column header="vividId" field="vividId" />
            <p-column header="Dewar" field="dewar" />
            <p-column header="Canister" field="canister" />
            <p-column header="Cryotop Color" field="plugColor" />
            <p-column header="Thawing Date" field="thawingDate" />
            <p-column header="Tech ID" field="techId" />
        </p-data-table>

        <p-button label="Edit" class="p-button-info p-mr-2" />
        <p-button label="Delete" class="p-button-danger" />
    </p-fieldset>
</template>

<script>
import { onMounted, ref } from "vue";
import { useSemenCryoStorageInit } from "@/composable/semen-cryo-storage";

import SemenCryoStorageForm from "@/components/semen-cryo-storage/form.vue";

export default {
    components: {
        SemenCryoStorageForm
    },
    setup() {
        const { getProvivers, getSemenCryoStorage } = useSemenCryoStorageInit();
        const table = ref([]);

        onMounted(() => {
            getProvivers();
            getSemenCryoStorage();
        });

        return {
            table
        };
    }
};
</script>

<style>
#semen-cryo-storage .p-card .p-card-content {
    padding: 0 0;
}

#semen-cryo-storage .p-field {
    margin-bottom: 0.5rem;
}

#semen-cryo-storage .p-card {
    height: 100%;
}
</style>
